#index-page{
    width: 100%;
    height: 100%;
}

#index-page{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: no-repeat fixed linear-gradient(-225deg, RGB(255, 223, 75, 0.5), RGB(0, 168, 255, 0.5));
    /* background-color: rgba(83, 92, 104, 0.1); */
}

.icon{
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
}

.login-form{
    display: flex;
    flex-direction: column;
    width:600px;
    height: 200px;
    background-color: azure;
    box-shadow: 2px 5px 10px 3px rgb(149, 165, 166);
    border-radius: 10px;
}

.login-text{
    align-self: flex-start;
    padding-top: 20px;
    padding-left: 80px;
}

.login-button{
    align-self: flex-end;
    padding-top: 20px;
    padding-right: 20px;
}
#main-page{
    display: flex;
    flex-direction: column;
}

main{
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 900px;
    margin-top: 50px;
    /* background-color: white; */
}

main #timeline-list{
    flex: 1 1;
    /* background-color: white; */
    border: 1px solid lightgray;
    padding-top: 50px;
    margin-bottom: 60px;
    padding-left: 20px;
    padding-right: 20px;
}

/* 타임라인 사진만 */

.timeline-1{
    display: flex;
    width: 100%;
    height: 400px;
    /* background-color: yellow; */
    border: 1px solid lightgray;
    border : 1px solid black;
}

.timeline-picture{
    /* background-color: honeydew; */
    border: 1px solid lightgray;
    flex: 6 1;
}

.timeline-picture>img{
    width: 480px;
    height: 400px;
    object-fit: cover;
}

.timeline-info{
    flex: 4 1;
    background-color: white;
}

.timeline-profile{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 60px;
    /* background-color: white; */
    border: 1px solid lightgray;
}

.timeline-profile .profile-picture{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    padding: 5px;
    border: 1px solid lightgray;
    /* z-index: 2; */
}

.timeline-profile .profile-picture>img{
    width: 50px;
    height: 50px;
    object-fit: cover;
}

.timeline-profile .profile-name{
    position: absolute;
    left: 0px;
    padding-left: 70px;
    display: flex;
    justify-content: left;
    align-items: center;
    width: 100%;
    
    height: 60px;
    /* background-color: salmon */
    border: 1px solid lightgray;
}

.timeline-comment{
    height: 340px;
    /* background-color: skyblue; */
    border: 1px solid lightgray;
}

.timeline-comment .status-message{
    height: 50px;
    /* background-color: azure; */
    border: 1px solid lightgray;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.timeline-comment .comment-list{
    height: 200px;
    /* background-color: tan; */
    border: 1px solid lightgray;
}

.timeline-comment .comment-buttons{
    height: 40px;
    /* background-color: snow; */
    border: 1px solid lightgray;
}

.timeline-comment .comment-input{
    height: 50px;
    /* background-color: coral; */
    border: 1px solid lightgray;
}

